
import React from 'react'
import { Table } from 'react-bootstrap';

export default function NewsFeed(props) {

    const { news } = props;


    const newsItems = news.map(item => {

        const { title, subtitle } = item;
        const { Y, M, D } = item.datetime;

        return (

            <tr key={item.id} className="w-100">
                <td className="w-75">
                    <h6>{title}</h6>
                    <p>{subtitle}</p>
                </td>
                <td className="float-right">
                    {/* Date */}
                    <span>{`${M}/${D}/${Y}`}</span>
                </td>
            </tr>
        );
    });



    return (
        <Table borderless={true} bordered={false} className="w-100" responsive={true}>
            <tbody>
                { newsItems }
            </tbody>
        </Table>
    );
}
