import React, { Component } from 'react'

import { Redirect,Link } from 'react-router-dom';
import { Row, Col, Card, Container, Table, Button, Tabs, Tab } from 'react-bootstrap';
import NewsFeed from './NewsFeed';

import { cases } from '../../case_data';
import { news } from '../../news_data';

export default class CaseView extends Component {

    constructor(props) {
        super(props);
        console.log(props);

        let { id } = props.match.params;
        id = parseInt(id);


        // Perform get request for data when backend implemented
        const caseHash = new Map();
        cases.forEach(caseObj => caseHash.set(caseObj.id, caseObj));

        const currCase = caseHash.get(id);

        this.state = {
            id: id,
            caseObj: currCase,
            validCase: caseHash.has(id),
        }
    }


    render() {

        const { caseObj, validCase } = this.state;
        if (validCase === false) {
            return <Redirect to="/case-feed" />
        }

        const { case_name, 
            plaintiffs, 
            defendents, 
            is_class_action, 
            court, 
            issue, 
            issue_description, 
            case_type, 
            compensation_ask,
            docket_number,
            file_date,
            post_date,
            incident_date } = caseObj;
        

        // Parse and Format data
        let actors_str = plaintiffs[0];
        for (let i = 1; i < plaintiffs.length; ++i) actors_str += `, ${plaintiffs[i]}`;
        actors_str += ` v. ${defendents[0]}`;
        for (let i = 1; i < defendents.length; ++i) actors_str += `, ${defendents[i]}`;

        const compensation_ask_str = `${compensation_ask}`;
        let format_compensation_ask = "";
        let i = 0;

        do {
            format_compensation_ask += compensation_ask_str[i];
            ++i;
        } while (i < compensation_ask_str.length && (compensation_ask_str.length - i)%3 !== 0);

        while (i < compensation_ask_str.length) {
            format_compensation_ask += `,${compensation_ask_str[i]}${compensation_ask_str[i+1]}${compensation_ask_str[i+2]}`;
            i += 3;
        }


        // Generate Related Cases
        const related_cases = cases.map(caseObj => {

            return (
                <Link to={`/case/${caseObj.id}`} onClick={e => this.setState({id: caseObj.id, caseObj: caseObj})}>
                    <Row key={caseObj.id} className="mb-2">
                        <Col>
                            <h6>{caseObj.case_name}</h6>
                            <span style={{fontSize: 9, color: 'green', marginRight:"4px"}}>Win 80%</span>
                            <span style={{fontSize: 9, color: 'black'}}>Ask: {caseObj.compensation_ask}</span>
                            <p style={{fontSize: 12, color: '#666666'}}>{caseObj.issue}</p>
                        </Col>
                    </Row>
                </Link>
            );
        });

        return (
            <Row>
                {/* Main Content */}
                <Col lg={8} md={8} sm={12} xs={12} style={{maxWidth: "600px"}}>
                    <Row>
                        <Col>
                            <Card>
                                <Card.Body>
                                    <h4 className="font-weight-bold">{case_name}</h4>
                                    <h6>{actors_str}</h6>
                                    <Row>
                                        <Col>
                                            <span>Docket #{docket_number}</span>
                                            <br />
                                            <span><i className="fa fa-map-marker"></i> {court}</span>
                                        </Col>
                                        <Col>
                                            {is_class_action && <React.Fragment><span><i className="fa fa-check"></i> Class Action</span><br /></React.Fragment>}
                                            <span><i className="fa fa-check"></i> {case_type}</span>
                                        </Col>
                                    </Row>
                                    <Row className="mt-4">
                                        <Col>
                                            <h5 className="font-weight-bold">Issue</h5>
                                            <Container>
                                                <h6 style={{fontStyle: "italic"}}>{issue}</h6>
                                                <p>{issue_description}</p>
                                            </Container>
                                        </Col>
                                    </Row>
                                    <Row className="mt-4">
                                        <Col>
                                            <h5 className="font-weight-bold">Dates <i className="fa fa-calendar"></i></h5>
                                            <Container>
                                                <Row>Date Filed: {`${file_date.M}/${file_date.D}/${file_date.Y}`}</Row>
                                                <Row>Date Posted: {`${post_date.M}/${post_date.D}/${post_date.Y}`}</Row>
                                                <Row>Date of Incident: {`${incident_date.M}/${incident_date.D}/${incident_date.Y}`}</Row>
                                            </Container>
                                        </Col>
                                        <Col>
                                            <h5 className="font-weight-bold">Social <i className="fa fa-share-alt"></i></h5>
                                            <Container>
                                                <Row>
                                                    <Col lg={2} md={2} sm={2} xs={2}>
                                                        <i className="fa fa-facebook"></i>
                                                    </Col>
                                                    <Col lg={2} md={2} sm={2} xs={2}>
                                                        <i className="fa fa-twitter"></i>
                                                    </Col>
                                                    <Col lg={2} md={2} sm={2} xs={2}>
                                                        <i className="fa fa-instagram"></i>
                                                    </Col>
                                                    <Col lg={2} md={2} sm={2} xs={2}>
                                                        <i className="fa fa-linkedin"></i>
                                                    </Col>
                                                </Row>
                                            </Container>
                                        </Col>
                                    </Row>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Tabs defaultActiveKey="news">
                                <Tab eventKey="news" title="News">
                                    <NewsFeed news={news} />
                                </Tab>
                                <Tab eventKey="social" title="Social">

                                </Tab>
                            </Tabs>
                        </Col>
                    </Row>
                </Col>

                {/*Investment Inteface/Related Cases*/}
                <Col lg={4} md={4} sm={12} xs={12} style={{maxWidth: '300px'}}>
                    {/* Investment Interface */}
                    <Row>
                        <Col>
                            <Table striped size="sm">
                                <tbody>
                                    <tr>
                                        <td>Win Probability</td>
                                        <td className="lead" style={{color: 'green'}}>78%</td>
                                    </tr>
                                    <tr>
                                        <td>Compensation Ask</td>
                                        <td>{`$${format_compensation_ask}`}</td>
                                    </tr>
                                    <tr>
                                        <td>Estimated Return</td>
                                        <td>{`$${format_compensation_ask}`}</td>
                                    </tr>
                                    <tr>
                                        <td>Desired Funding</td>
                                        <td>$25,000</td>
                                    </tr>
                                </tbody>
                            </Table>

                            <Button variant="primary" className="w-100">Buy This Case</Button>
                            <Button variant="outline-primary" className="w-100">Sell This Case</Button>
                        </Col>
                    </Row>


                    {/* Related Cases Nav */}
                    <Row>
                        <Col className="mt-4">
                            <Card>
                                <Card.Header>
                                    <Card.Title as="h5">Related Cases <i className="fa fa-gavel"></i></Card.Title>
                                </Card.Header>
                                <Card.Body>
                                    {related_cases}
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
}
